.App {
  text-align: center;
}

.App-header {
  display: flex;
  flex-wrap: wrap;
  background-color: #282c34;
  height: 100vh;
}

.box-container{
  display: flex;
  width:100%;
}

.box{
  height: 100px;
  width: 100%;
}

#pink{
  background-color: hotpink;
}

#blue{
  background-color: dodgerblue;
}

#green{
  background-color: greenyellow;
}

.displayed-card{
  background-color: #282c34;
  color: white;
  font-size: 16px;
  border: solid 2px white;
  width: 100%;
  height: 50px;

}


.shake-button {
  height: 50px;
  width: 100%;
  background-color: yellow;
  color: black;
  font-size: 12px;
  position: absolute;
  bottom: 0;
  left: 0;
}

